import React, { useState, useEffect } from 'react';
import mempoolJS from '@mempool/mempool.js'
import axios from 'axios'
import {
  Paper,
  Stack,
  Box
} from '@mui/material';
import { styled } from '@mui/material/styles';

const base64 = require('base64-js');

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  boxShadow: 'none'
}));

let raw;
let pointer;
let txs = [];

let timer;

const { bitcoin: { websocket, transactions, mempool  } } = mempoolJS({
  hostname: 'mempool.space'
});

const ws = websocket.initClient({
  options: ['stats'],
});

function GetMemPool() {

  const [ordinals, setOrdinals] = useState([])

  const init = async () => {
    getRecentTx();
    timer =setInterval(getRecentTx,6000)
  };

  async function getRecentTx(){
    const getMempoolRecent = await mempool.getMempoolRecent();
    if(getMempoolRecent.length > 0 ){
      for(let i = 0; i < getMempoolRecent.length; i++){
        if (!txs.includes(getMempoolRecent[i].txid.toLowerCase())) {
          txs.push(getMempoolRecent[i].txid.toLowerCase())
          if(txs.length > 400){
            const txsTemp = txs.slice(0, 200)
            txs = txsTemp                        
          }
          await parseContent(getMempoolRecent[i].txid, true)
        }        
      }     
    }
  }

  async function getRawData(txId) {
    const url = `https://mempool.space/api/tx/${txId}`;
    try {
      const response = await axios.get(url);
      const txWitness = response.data.vin[0].witness.join('');
      return Buffer.from(txWitness, 'hex');
    } catch (error) {
      process.exit?.(1)
    }
  }

  function readBytes(n = 1) {
    try {
      const value = raw.slice(pointer, pointer + n);
      pointer += n;
      return value;
    } catch (e) { }

  }

  function getInitialPosition() {
    const inscriptionMark = Buffer.from('0063036f7264', 'hex');
    try {
      return raw.indexOf(inscriptionMark) + inscriptionMark.length;
    } catch (error) {
      process.exit?.(1)
    }
  }

  function readContentType() {
    try {
      const OP_1 = Buffer.from('51', 'hex');
      const byte = readBytes();
      if (!byte.equals(OP_1)) {
        if (!byte.equals(Buffer.from('01', 'hex')) || !readBytes().equals(Buffer.from('01', 'hex'))) {
          process.exit?.(1)
        }
      }

      const size = readBytes().readUIntBE(0, 1);
      const contentType = readBytes(size).toString('utf8');
      return contentType;
    } catch (e) { }

  }

  function readPushData(opcode) {
    try {
      const intOpcode = opcode.readUIntBE(0, 1);

      if (intOpcode >= 0x01 && intOpcode <= 0x4b) {
        return readBytes(intOpcode);
      }

      let numBytes = 0;
      switch (intOpcode) {
        case 0x4c:
          numBytes = 1;
          break;
        case 0x4d:
          numBytes = 2;
          break;
        case 0x4e:
          numBytes = 4;
          break;
        default:
          process.exit?.(1)
      }

      const size = readBytes(numBytes).readUIntLE(0, numBytes);
      return readBytes(size);
    } catch (e) { }

  }

  const jumpTo = (tx_id) => {
    const w = window.open('_black')
    let url = `https://mempool.space/tx/${tx_id}`
    w.location.href = url
  }


  function isJsonContent(content) {
    try {
      JSON.parse(content.toString('utf8'));
      return true;
    } catch (e) {
      return false
    }
  }  

  function writeDataURI(tx_id, data, contentType) {
    try {
      if (contentType.indexOf('image') >= 0) {
        const dataBase64 = base64.fromByteArray(data).replace(/\n/g, '');
        const imgData = `data:${contentType};base64,${dataBase64}`
        const imgElement =
          <Item sx={{
            width: 200,
            height: 200,
            margin: 2,
            alignItems: 'center',
            justifyContent: 'center',
            display: 'flex',
            overflow: 'hidden',
            backgroundColor: '#000',
            cursor: 'pointer'
          }}
            onClick={() => jumpTo(tx_id)}
          >
            <img width={128} height={128} key={Math.floor(Math.random() * 100000000)} src={imgData} />
          </Item>
        setOrdinals(qev => [imgElement, ...(qev.length > 500 ? qev.slice(0, 500) : qev)])
      } else if (contentType.indexOf('text') >= 0) {
        const isJson = isJsonContent(data)
        const txtElement =
          <Item sx={{
            width: 200,
            height: 200,
            margin: 2,
            alignItems: 'center',
            justifyContent: 'center',
            display: 'flex',
            backgroundColor: '#000',
            cursor: 'pointer'
          }}>
            <div key={Math.floor(Math.random() * 100000000)}>
              <pre
                style={{
                  textAlign: 'left',
                  vlign: 'center',
                  fontWeight: 100,
                  color: '#fff',
                  maxWidth: 200,
                  whiteSpace: 'pre-wrap',
                  wordWrap: 'break-word',
                  cursor: 'pointer'
                }}
                onClick={() => jumpTo(tx_id)}
              >
                {isJson
                  ? JSON.stringify(JSON.parse(data.toString('utf8')), null, 2)
                  : data.toString('utf8')
                }
              </pre>
            </div>
          </Item>
        setOrdinals(qev => [txtElement, ...(qev.length > 500 ? qev.slice(0, 500) : qev)])
      } else {

      }
    } catch (e) {
    }
  }

  function getFileExtension(contentType) {
    try {
      const switcher = {
        'text/plain;charset=utf-8': 'txt',
        'text/html;charset=utf-8': 'html',
      };
      return switcher[contentType] || contentType.split('/')[1];
    } catch (e) {
      return 'unknow'
    }
  }

  async function parseContent(tx_id, data_uri) {
    try {
      raw = await getRawData(tx_id);
      pointer = getInitialPosition();

      const contentType = readContentType();
      const fileExtension = getFileExtension(contentType);

      if (!readBytes().equals(Buffer.from('00', 'hex'))) {
        process.exit?.(1)
      }

      let data = Buffer.alloc(0);

      const OP_ENDIF = Buffer.from('68', 'hex');
      let opcode = readBytes();
      while (!opcode.equals(OP_ENDIF)) {
        const chunk = readPushData(opcode);
        data = Buffer.concat([data, chunk]);
        opcode = readBytes();
      }
      writeDataURI(tx_id, data, contentType);
    } catch (e) {

    }
  }

  useEffect(() => {
    init();
    return(()=>clearInterval(timer))
  }, []);

  return (
    <>
      <div>
        <Stack
          direction="row"
          justifyContent="flex-start"
          alignItems="center"
          spacing={2}
          sx={{ marginTop: 4, marginBottom: 2 }}
        >
          <Item style={{ fontWeight: 600 }}>Site: https://ordi.verybox.xyz</Item>
          <Item>Data from: <a target="blank" href='https://www.mempool.space'> {'https://mempool.space/'} </a> </Item>
          <Item>Twitter:: <a target="blank" href='https://www.twitter.com/veryboxXyz'> {'@veryboxXyz'} </a> </Item>
        </Stack>
      </div>
      <Box sx={{
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
      }} >
        {ordinals}
      </Box>
    </>

  );
}

export default GetMemPool;